<script>
import axios from "axios";

export default {
  data() {
    return {
      page: 1,
      perPage: 3,
      contentsData: [],
      items: [
        { title: "Click Me" },
        { title: "Click Me" },
        { title: "Click Me" },
        { title: "Click Me 2" },
      ],
      activeBtn: 1,
      colors: [
        "indigo",
        "warning",
        "pink darken-2",
        "red lighten-1",
        "deep-purple accent-4",
      ],
      slides: ["First", "Second", "Third", "Fourth", "Fifth"],
    };
  },
  methods: {},
  computed: {
    visiblePages() {
      return this.contentsData.slice(
        (this.page - 1) * this.perPage,
        this.page * this.perPage
      );
    },
  },
  mounted() {
    axios
      .get(
        "http://localhost:8080/hrdi-admin/api/content/getContentFirstPage.php"
      )
      .then((response) => {
        if (response.data.content.length > 0) {
          this.contentsData = response.data.content;
          response.data.content.map((content, index) => {
            this.contentsData[
              index
            ].contentImg = `http://localhost:8080/hrdi-admin/upload/${content.contentImg}`;
          });
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
</script>
