<template>
  <div>
    <v-container>
      <p class="display-1 font-weight-light	text-center pa-4">แนะนำผลผลิต</p>
      <div class="row">
        <div
          class="col-md-4 col-sm-4 col-md-4"
          v-for="content in visiblePages"
          :key="content.id"
        >
          <v-hover v-slot="{ hover }">
            <v-card
              class="mx-auto"
              max-width="400"
              height="100%"
              :elevation="hover ? 16 : 2"
            >

              <v-img
                class="white--text align-end"
                height="300px"
                :src="content.contentImg"
                @click="$router.push({name: 'Post', params: { id: content.contentId, page: 1}})"
              >
              </v-img>

              <v-card-title class="pb-0 font-weight-black text-primary">
                {{ content.contentTitle }}
              </v-card-title>

              <v-card-text>
                <v-chip-group
                  v-model="selection"
                  active-class="green darken-3  white--text"
                  column
                >
                  <v-chip> {{ content.contentTypeTitle }} </v-chip>
                </v-chip-group>
              </v-card-text>

              <v-card-actions>
                <v-btn
                  color="orange"
                  text
                  @click="$router.push({name: 'Post', params: { id: content.contentId }})"
                >
                  อ่านเพิ่มเติม
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-hover>
        </div>
      </div>
    </v-container>
    <div class="text-center mb-3">
      <v-pagination
        v-model="page"
        :length="Math.ceil(contentData.length/perPage)"
      ></v-pagination>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      page: 1,
      perPage: 9,
      contentType: [],
      contentData: [],
      selection: 0,
    };
  },
  computed: {
    visiblePages() {
      return this.contentData.slice(
        (this.page - 1) * this.perPage,
        this.page * this.perPage
      );
    },
  },
  methods: {
    toThaiDateString(date) {
      let monthNames = [
        "มกราคม",
        "กุมภาพันธ์",
        "มีนาคม",
        "เมษายน",
        "พฤษภาคม",
        "มิถุนายน",
        "กรกฎาคม",
        "สิงหาคม.",
        "กันยายน",
        "ตุลาคม",
        "พฤศจิกายน",
        "ธันวาคม",
      ];

      let year = date.getFullYear() + 543;
      let month = monthNames[date.getMonth()];
      let numOfDay = date.getDate();

      let hour = date.getHours().toString().padStart(2, "0");
      let minutes = date.getMinutes().toString().padStart(2, "0");
      let second = date.getSeconds().toString().padStart(2, "0");

      return (
        `${numOfDay} ${month} ${year} ` + `${hour}:${minutes}:${second} น.`
      );
    },
  },
  mounted() {
    axios
      .get("http://localhost:8080/hrdi-admin/api/content/getContent.php")
      .then((response) => {
        if (response.data.content.length > 0) {
          const onlyPublish = response.data.content.filter((c) => {
            return c.contentPublish !== '0'
          })
          this.contentData = onlyPublish;
          onlyPublish.map((content, index) => {
            this.contentData[
              index
            ].contentImg = `http://localhost:8080/hrdi-admin/upload/${content.contentImg}`;
          });
          this.contentData.reverse();
          console.log(this.contentData);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
</script>



