<script>
import axios from "axios";
import ThailandAutoComplete from "vue-thailand-address-autocomplete";

export default {
  components: {
    ThailandAutoComplete,
  },
  data: () => ({
    isSelectProvince: false,
    isFormValid: false,
    confirmPassword: "",
    sellerForm: {
      idTypeOfSeller: "",
      prefix: "",
      firstName: "",
      lastName: "",
      username: "",
      phoneNumber: "",
      email: "",
      idCard: "",
      gender: "",
      password: "",
      vProvinceId: "",
      vLinkAreaDetailId: "",
      productType: [],
      address: "",
      moo: "",
      road: "",
      district: "",
      amphoe: "",
      zipcode: "",
      groupName: "",
      shopName: "",
      brandName: "",
      lineId: "",
      facebookName: "",
    },
    isRegisForm: {
      buyer: false,
      seller1: false,
    },
    isAgree: "",
    valid: false,
    userType: "",
    isType: true,
    isShowPolicy: false,
    policy: {
      showBuyerPolicy: false,
      showSellerPolicy: false,
      title: "",
      titleColor: "",
      ruleTitle: "",
      firstRule: "",
      secRule: "",
      thirdRule: "",
      fourthRule: "",
      fifthRule: "",
      sixthRule: "",
      seventhRule: "",
      eigthRule: "",
      ninethRule: "",
      footer: "",
    },
    prefix: [],
    area: [],
    sellType: [],
    province: [
      {
        provinceName: "",
      },
    ],
    targetName: [],
    targetFilter: [],
    firstNameRules: [(v) => !!v || "กรุณากรอกชื่อ"],
    lastNameRules: [(v) => !!v || "กรุณากรอกนามสกุล"],
    emailRules: [
      (v) => !!v || "กรุณากรอกอีเมลล์",
      (v) => /.+@.+/.test(v) || "อีเมลล์ต้องอยู่ในรูปแบบที่ถูกต้อง",
    ],
    phoneRules: [
      (v) => !v.length < 10 || "หมายเลขโทรศัพท์ต้องมีจำนวน 10 หลัก",
      (v) => !!v || "กรุณากรอกหมายเลขโทรศัพท์",
      (v) =>
        /^0[0-9]{9}/.test(v) || "หมายเลขโทรศัพท์ต้องอยู่ในรูปแบบที่ถูกต้อง",
    ],
    cardRules: [
      (v) => !!v || "กรุณากรอกเลขบัตรประชาชน",
      (v) => v.length >= 13 || "หมายเลขบัตรประชาชนต้องมีจำนวน 13 หลัก",
    ],
    passwordRules: [
      (v) => !!v || "กรุณากรอกรหัสผ่าน",
      (v) => v.length >= 6 || "รหัสผ่านต้องมีอย่างน้อย 6 ตัว",
      (v) => /[A-Z]+/.test(v) || "ต้องมีตัวอักษรตัวใหญ่ในรหัสผ่าน",
      (v) => /.{8,}/.test(v) || "ตัองมีตัวเลขในรหัสผ่าน",
      (v) => /^[a-zA-Z0-9äöüÄÖÜ]*$/.test(v) || "ไม่อนุญาตให้ใช้ตัวอักษรพิเศษ",
      (v) =>
      (v || "").indexOf(" ") < 0 || "ไม่อนุญาตให้ใช้รหัสผ่านที่มีช่องว่าง",
    ],
  }),
  methods: {
    select(address) {
      this.sellerForm.district = address.district;
      this.sellerForm.amphoe = address.amphoe;
      this.sellerForm.zipcode = address.zipcode;
    },
    findAreaByProvince(provinceId) {
      axios
        .get(
          `https://farmtd.hrdi.or.th/farmdd/api/getVLinkTargetById.php?provinceId=${provinceId}`
        )
        .then((res) => {
          console.log("vTargetDetail", res.data.vTargetDetail);
          if (res.data.vTargetDetail.length > 0) {
            this.targetName = [
              ...new Map(
                res.data.vTargetDetail.map((item) => [item.targetName, item])
              ).values(),
            ];
            this.getArea();
          }
        });
      this.isSelectProvince = true;
    },
    getArea() {
      axios
        .get(`https://farmtd.hrdi.or.th/farmdd/api/getAllArea.php`)
        .then((res) => {
          this.targetName = this.targetName.map((item) => item.targetId);
          console.log("targetName", this.targetName);
          if (res.data.area.length > 0) {
            this.area = res.data.area;
          }
          this.targetFilter = res.data.area.filter((item) =>
            this.targetName.includes(item.idArea)
          );
          this.isSelectProvince = true;
          console.log("targetFilter", this.targetFilter);
        });
    },
