    acceptPolicyCheck(isAccept, Type) {
      console.log(isAccept);
      if (!isAccept || isAccept === "Unaccecpt" || isAccept === null) {
        this.$swal.fire({
          icon: "warning",
          title: "กรุณาตรวจสอบ",
          text: "กรุณายอมรับข้อตกลง",
          allowOutsideClick: false,
        });
        return;
      }

      this.isShowPolicy = false;

      if (isAccept && Type === "Buyer") {
        this.isRegisForm = {
          buyer: true,
          seller1: false,
        };
        console.log(this.isRegisForm);
      }

      if (isAccept && Type === "Seller") {
        this.isRegisForm = {
          buyer: false,
          seller1: true,
        };
      }
    },
    checkSellerRegister() {
      console.log(this.sellerForm);
      if (
        this.sellerForm.prefix === "" ||
        this.sellerForm.prefix === null ||
        this.sellerForm.prefix === undefined
      ) {
        this.alertCheck("กรุณาเลือกคำนำหน้า");
        return;
      }

      if (
        this.sellerForm.firstName === "" ||
        this.sellerForm.firstName === null ||
        this.sellerForm.firstName === undefined
      ) {
        this.alertCheck("กรุณากรอกชื่อ");
        return;
      }

      if (
        this.sellerForm.lastName === "" ||
        this.sellerForm.lastName === null ||
        this.sellerForm.lastName === undefined
      ) {
        this.alertCheck("กรุณากรอกนามสกุล");
        return;
      }

      if (
        this.sellerForm.email === "" ||
        this.sellerForm.email === null ||
        this.sellerForm.email === undefined
      ) {
        this.alertCheck("กรุณากรอกอีเมลล์");
        return;
      }

      if (
        this.sellerForm.idCard === "" ||
        this.sellerForm.idCard === null ||
        this.sellerForm.idCard === undefined
      ) {
        this.alertCheck("กรุณากรอกเลขบัตรประชาชน");
        return;
      }

      if (
        this.sellerForm.idCard === "" ||
        this.sellerForm.idCard === null ||
        this.sellerForm.idCard === undefined
      ) {
        this.alertCheck("กรุณาเลือกเพศ");
        return;
      }

      var formData = new FormData();
      formData.append("address", this.sellerForm.address);
      formData.append("amphoe", this.sellerForm.amphoe);
      formData.append("brandName", this.sellerForm.brandName);
      formData.append("district", this.sellerForm.district);
      formData.append("email", this.sellerForm.email);
      formData.append("facebookName", this.sellerForm.facebookName);
      formData.append("firstName", this.sellerForm.firstName);
      formData.append("gender", this.sellerForm.gender);
      formData.append("groupName", this.sellerForm.groupName);
      formData.append("idCard", this.sellerForm.idCard);
      formData.append("idTypeOfSeller", this.sellerForm.idTypeOfSeller);
      formData.append("lastName", this.sellerForm.lastName);
      formData.append("lineId", this.sellerForm.lineId);
      formData.append("moo", this.sellerForm.moo);
      formData.append("password", this.sellerForm.password);
      formData.append("phoneNumber", this.sellerForm.phoneNumber);
      formData.append("prefix", this.sellerForm.prefix);
      formData.append("productType", this.sellerForm.productType);
      formData.append("road", this.sellerForm.road);
      formData.append("shopName", this.sellerForm.shopName);
      formData.append("username", this.sellerForm.username);
      formData.append("vLinkAreaDetailId", this.sellerForm.vLinkAreaDetailId);
      formData.append("vProvinceId", this.sellerForm.vProvinceId);
      formData.append("zipcode", this.sellerForm.zipcode);
      var contentForm = {};
      formData.forEach(function (value, key) {
        console.log(value);
        contentForm[key] = value;
      });

      axios
        .post(
          "https://farmtd.hrdi.or.th/farmdd/api/postAddSellerMember.php",
          formData
        )
        .then((res) => {
          if (res.data === "True") {
            this.$swal
              .fire({
                icon: "success",
                title: "สมัครสมาชิกสำหรับผู้ขายสำเร็จ",
                text: "รอแอดมินตรวจสอบข้อมูลเพื่อยืนยันการสมัครสมาชิก",
                showConfirmButton: false,
                timer: 2000,
              })
              .then(() => {
                this.$router.push({ name: "Login" });
              });
          } else {
            this.$swal
              .fire({
                icon: "error",
                title: "เกิดข้อผิดพลาด",
                text: "ไม่สามารถสมัครสมาชิกได้",
                showConfirmButton: false,
                timer: 2000,
              })
              .then(() => {
                this.$router.push({ name: "Login" });
              });
          }
        });
    },
    alertCheck(msg) {
      this.$swal.fire({
        icon: "warning",
        title: msg,
        allowOutsideClick: false,
      });
    },
    getPrefix() {
      axios
        .get("https://farmtd.hrdi.or.th/farmdd/api/getPrefix.php")
        .then((res) => {
          this.prefix = res.data.prefix;
          console.log("prefix", this.prefix);
        });
    },
    getProvince() {
      axios
        .get("https://farmtd.hrdi.or.th/farmdd/api/getVLinkProvince.php")
        .then((res) => {
          if (res.data.vLinkProvince.length > 0) {
            this.province = [
              ...new Map(
                res.data.vLinkProvince.map((item) => [item.provinceId, item])
              ).values(),
            ];
          }

          this.province = this.province.filter((item, index) => {
            return (
              item.provinceName !== "เลย" &&
              item.provinceName !== "ลำปาง" &&
              item.provinceName !== "แพร่"
            );
          });

          console.log("province", this.province);
        });
    },
    seller2Revert() {
      this.isRegisForm = {
        seller1: true,
        seller2: false,
        seller3: false,
      };
    },
    getSellerType() {
      axios
        .get("https://farmtd.hrdi.or.th/farmdd/api/getSellerType.php")
        .then((res) => {
          this.sellType = res.data.sellType;
          console.log("sellType", this.sellType);
        });
    },
  }, // End Method
  mounted() {
    this.getPrefix();
    this.getProvince();
    this.getSellerType();
  },
};
</script>
<style scoped>
#rounded {
  border-radius: 5px;
}
</style>



