<template>
  <v-container
    fluid
    fill-height
  >
    <v-layout
      align-center
      justify-center
    >
      <v-flex
        x
        sm8
        md4
      >
        <v-card class="elevation-12">
          <v-toolbar
            dark
            color="primary"
          >
            <v-toolbar-title>ของดีบนดอย Login</v-toolbar-title>
          </v-toolbar>

          <v-col class="md-3">
            <v-alert
              type="error"
              color="red lighten-1"
              v-if="isEmtyp"
            >
              ไม่สามารถเข้าสู่ระบบได้ กรุณาตรวจสอบ Username หรือ Password
            </v-alert>
          </v-col>

          <v-col class="md-6">
            <v-img
              id="rounded"
              class="white--text align-end"
              height="300px"
              :src="require('../assets/img/home/login-logo.jpeg')"
            >
            </v-img>
          </v-col>

          <v-card-text>
            <v-form>
              <v-text-field
                prepend-icon="mdi-account"
                name="login"
                label="Login"
                type="text"
                v-model="username"
                :rules="usernameRules"
              ></v-text-field>
              <v-text-field
                id="password"
                prepend-icon="mdi-lock"
                name="password"
                label="Password"
                type="password"
                v-model="password"
                :rules="passwordRules"
              ></v-text-field>
            </v-form>
          </v-card-text>

          <v-card-actions>
            <router-link
              to="register"
            >
              <v-card-title class="subtitle-1" color="primary">
                Create Account
              </v-card-title>
            </router-link>
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              @click="checkLogin"
            >Login</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    valid: false,
    username: "",
    isEmtyp: false,
    usernameRules: [(v) => !!v || "Username is required"],
    password: "",
    passwordRules: [(v) => !!v || "Password is required"],
  }),
  methods: {
    checkLogin() {
      this.isEmtyp = false;

      if (
        this.username === "" ||
        this.username === null ||
        this.username === undefined
      ) {
        this.isEmtyp = true;
        return;
      }

      if (
        this.password === "" ||
        this.password === null ||
        this.password === undefined
      ) {
        this.isEmtyp = true;
        return;
      }
    },
  },
};
</script>
<style scoped>
#rounded {
  border-radius: 5px;
}
</style>



